"active\">\r\n        define applicators.\r\n      </a>\r\n    </p>\r\n  </div>\r\n\r\n  <div \r\n      *ngIf=\"this.machineList.length > 0 && currentSettings.machine != null\" \r\n      class=\"mdl-cell mdl-cell--3-col mdl-cell--4-col-tablet mdl-cell--2-col-phone\">\r\n    <h6>SSD</h6>\r\n    <select  \r\n        [(ngModel)]=\"currentSettings.ssd\"\r\n        (input)=\"updateSSD($event.target.value)\"\r\n        *ngIf=\"currentMachineSpecifications.ssd.length > 0\">\r\n      <option *ngFor=\"let ssd of currentMachineSpecifications.ssd\">{{ssd}}</option>\r\n    </select>\r\n    <p *ngIf=\"currentMachineSpecifications.ssd == 0\">\r\n      This machine doesn't have an ssd defined. Please \r\n      <a routerLink=\"/specifications\" routerLinkActive=\"active\">\r\n        define at least one ssd.\r\n      </a>\r\n    </p>\r\n  </div>\r\n</div>\r\n  \r\n";

